<template><section><h1>radio 单选框</h1>
<hr>
<p>单选框通常用于一组选项中，只能单独选择一项的情况。</p>
<h2>何时使用</h2>
<p>用于在多个备选项中选中单个状态。</p>
<p>和 Select 的区别是，Radio 所有选项默认可见，方便用户在比较中选择，因此选项不宜过多。</p>
<h2>基本单选框</h2>
<p>最简单的用法。</p>
<demo-box>
                    <div slot="demo"><html><head></head><body><cat-radio v-model="radio" label="1">选项一</cat-radio>
</body></html></div>
                    <div slot="source-code"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">cat-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"1"</span>&gt;</span>选项一<span class="hljs-tag">&lt;/<span class="hljs-name">cat-radio</span>&gt;</span>
</code></pre>
</div></demo-box>

</section></template>
<style lang="scss" scoped>
  .cat-radio {
    margin: 10px 10px 0 0;
  }

  p {
    line-height: 35px;
  }
</style>
<script>
export default {
  data () {
    return {
      radio: ''
    }
  }
}
</script>


